<template>
  <div class="game-scene" :class="{'game-scene__win': isUserWin, 'game-scene__lose': !isUserWin}">

    <AppPopUp :text="textResult" :img-url="isUserWin  ?
    '/evil_goblin//img/frames/frame-success.png' :
    '/evil_goblin//img/frames/frame-fail.png'"/>

    <AppScope/>


    <AppButtonGroup>
      <AppButton class="button-group__button_green"
                 @click="nextLevel()">–î–∞–ª–µ–µ</AppButton>
    </AppButtonGroup>
    <canvas ref="goblin" class="goblin" >
      –û–±–Ω–æ–≤–∏—Ç–µ –±—Ä–∞—É–∑–µ—Ä, –∫ —Å–æ–∂–∞–ª–µ–Ω–∏—é –í–∞—à–∞ –≤–µ—Ä—Å–∏—è –±—Ä–∞—É–∑–µ—Ä–∞ –Ω–µ –ø–æ–¥–¥—Ä–µ–∂–∏–≤–∞–µ—Ç—Å—è üòø
    </canvas>

  </div>
</template>

<script>
import resource from '@/helper/Resource';
import EvilGoblin from '@/models/EvilGoblin';
import AppScope from '@/components/common/AppScope.vue';
import AppPopUp from '@/components/common/AppPopUp';
import AppButtonGroup from '@/components/common/AppButtonGroup';
import AppButton from '@/components/common/AppButton';

export default {
  name: 'LevelFinish',
  components: {
    AppButton, AppButtonGroup, AppPopUp, AppScope,
  },
  props: {
    levelFinish: {
      type: Boolean,
      require: true,
    },
  },
  data() {
    return {
      frameId: null,
      canvas: null,
      context: null,
      lastTime: null,
      gameTime: 0,
      speed: 300,
      goblin: new EvilGoblin({ position: [-10, -10] }),
    };
  },

  computed: {
    isUserWin() {
      return this.$store.state.scope > 0;
    },
    textResult() {
      if (this.isUserWin) {
        return '–¢—ã —Å–ø—Ä–∞–≤–∏–ª–∞—Å—å —Å –∑–∞–¥–∞–Ω–∏–µ–º –≥–æ–±–ª–∏–Ω–∞, –∏ –µ–º—É –Ω–∏—á–µ–≥–æ –Ω–µ –æ—Å—Ç–∞–µ—Ç—Å—è, '
          + '–∫–∞–∫ –ø—Ä–æ–ø—É—Å—Ç–∏—Ç—å —Ç–µ–±—è –∫ —Å–ª–µ–¥—É—é—â–∏–º –∏—Å–ø—ã—Ç–∞–Ω–∏—è–º. ¬´–ù–æ –Ω–µ —Ä–∞–¥—É–π—Å—è —Å–ª–∏—à–∫–æ–º —Ä–∞–Ω–æ,'
          + ' - –∑–ª–æ–±–Ω–æ —à–∏–ø–∏—Ç –ì–æ–±–ª–∏–Ω —Ç–µ–±—è –≤ —Å–ª–µ–¥, - –º—ã —Å —Ç–æ–±–æ–π –µ—â—ë –≤—Å—Ç—Ä–µ—Ç–∏–º—Å—è, –∏ —Ç–æ–≥–¥–∞ –ø–æ—Å–º–æ—Ç—Ä–∏–º, –∫—Ç–æ –∫–æ–≥–æ!';
      }
      return '–£–≤—ã, —Ç—ã –Ω–µ —Å–ø—Ä–∞–≤–∏–ª—Å—è —Å –∑–∞–¥–∞–Ω–∏–µ–º –≥–æ–±–ª–∏–Ω–∞. –ù–æ –≤–Ω–µ–∑–∞–ø–Ω–æ –≤ –µ–≥–æ —Ö–æ–ª–æ–¥–Ω–æ–µ, '
          + '–∑–ª–æ–±–Ω–æ–µ —Å–µ—Ä–¥—Ü–µ –∑–∞–∫—Ä–∞–ª–∞—Å—å –¥–æ–±—Ä–æ—Ç–∞: –ì–æ–±–ª–∏–Ω —Å–æ–≥–ª–∞—à–∞–µ—Ç—Å—è –ø—Ä–æ–ø—É—Å—Ç–∏—Ç—å —Ç–µ–±—è. ¬´–ù–æ —Å–º–æ—Ç—Ä–∏, - '
          + '–≤–æ—Ä—á–∏—Ç –æ–Ω, - –Ω–µ —Ä–∞–∑–æ—á–∞—Ä—É–π –º–µ–Ω—è: —Å–æ —Å–ª–µ–¥—É—é—â–∏–º–∏ –∑–∞–¥–∞–Ω–∏—è–º–∏ —Ç—ã –¥–æ–ª–∂–µ–Ω —Å–æ–≤–ª–∞–¥–∞—Ç—å –≥–æ—Ä–∞–∑–¥–æ –ª—É—á—à–µ.';

      return this.$store.state.scope > 0;
    },
  },
  watch: {
    canvas() {
      this.goblin.updateDefaultState('repeatFinish');
    },
  },
  methods: {
    init() {
      const { $refs } = this;
      this.canvas = $refs.goblin;
      const { canvas } = this;
      if (canvas.getContext) {
        canvas.width = 350; // goblin size
        canvas.height = 350;
        this.context = canvas.getContext('2d');
      }
      this.setGoblin();
      this.updateGame();
    },

    nextLevel() {
      this.$emit('startGame');
    },

    setGoblin() {
      this.goblin.animate(this.context);
    },

    updateGame() {
      if (this.levelFinish) {
        return;
      }
      const now = Date.now();
      const dt = (now - this.lastTime) / 1000.0;

      this.update(dt);

      this.lastTime = now;
      this.frameId = window.requestAnimationFrame(this.updateGame);
    },
    update(dt) {
      const { context, canvas } = this;
      this.gameTime += dt;

      context.clearRect(0, 0, canvas.width, canvas.height);
      this.setGoblin();
    },
  },
  mounted() {
    resource.onReady(this.init);
  },
  beforeDestroy() {
    cancelAnimationFrame(this.frameId);
  },
};
</script>

<style lang="scss" scoped>

  .game-scene {
    flex-grow: 1;
    background-repeat: no-repeat;
    background-position: center center;
    &__win {
      background-image: url("/evil_goblin//img/bg/bg-5.jpg");
    }
    &__lose {
      background-image: url("/evil_goblin//img/bg/bg-3.jpg");
    }
  }

  .goblin {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 350px;
    height: 350px;
  }

  .button-group {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    &__button {
      color: white;
      border: 0;
      padding: 10px;
      margin: 0 5px 10px 5px;
      min-width: 200px;
      font-size: 18px;
      border-radius: 10px;
    }

    &__button_green {
      background-color: green;
    }
  }
</style>
