import resource from '@/helper/Resource';

export default class EvilGoblin {
  constructor({
    size = [350, 350],
    sizeImg = [2048, 2048],
    position = [document.body.clientWidth - 360, document.body.clientHeight - 360],
  }) {
    this.state = 'default';
    this.defaultState = 'default';
    this.variables = {
      default: [1],
      angry: [1, 2, 3],
      claps: [1, 2],
      happy: [1, 2],
      repeat: [1, 2],
      repeatFinish: [1],
    };
    this.playCount = 1;
    this.fullPlayCount = 0;
    this.timeAnimationPlay = 30;
    this.interval = null;
    this.timer = 0;
    this.size = size;
    this.position = position;
    this.sizeImg = sizeImg;
  }

  updateState(state) {
    this.state = state;
    this.clearAnimate();
  }

  updateDefaultState(state) {
    this.defaultState = state;
    this.clearAnimate();
  }

  clearAnimate() {
    clearInterval(this.interval);
    this.playCount = 1;
    this.interval = null;
    this.timer = 0;
  }

  animate(ctx) {
    if (!this.interval) {
      this.interval = setInterval(() => {
        this.timer += 1;
      }, 1);
    }
    const { state, variables, playCount } = this;
    ctx.drawImage(resource.get(`/evil_goblin//img/gnom/goblin_${state}_${this.playCount}.png`),
      0, 0,
      this.sizeImg[0], this.sizeImg[1], //
      this.position[0], this.position[1], // position goblin in canvas
      this.size[0], this.size[1]);

    if (this.fullPlayCount === variables[state].length + 3) {
      this.updateState(this.defaultState);
      this.fullPlayCount = 0;
    }

    if (this.timeAnimationPlay <= this.timer) {
      this.playCount += 1;
      if (variables[state].length - 1 < playCount) {
        this.playCount = 1;
        this.fullPlayCount += 1;
      }
      clearInterval(this.interval);
      this.interval = null;
      this.timer = 0;
    }
  }
}
