import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    scope: 0,
  },
  mutations: {
    addScope(state) {
      let addValue = 10;
      const interval = setInterval(() => {
        if (addValue <= 0) {
          clearInterval(interval);
          return;
        }
        state.scope += 1;
        addValue -= 1;
      }, 30);
    },
    clearScope(state) {
      state.scope = 0;
    },
  },
  actions: {

  },
});
