<template>
    <div style="width: 100%;height: 100%">
      <canvas ref="game" class="game_scene_1" >
        –û–±–Ω–æ–≤–∏—Ç–µ –±—Ä–∞—É–∑–µ—Ä, –∫ —Å–æ–∂–∞–ª–µ–Ω–∏—é –í–∞—à–∞ –≤–µ—Ä—Å–∏—è –±—Ä–∞—É–∑–µ—Ä–∞ –Ω–µ –ø–æ–¥–¥—Ä–µ–∂–∏–≤–∞–µ—Ç—Å—è üòø
      </canvas>

      <AppTimer :time-end="timeEnd"/>
      <AppScope/>


      <AppButtonGroup>
        <AppButton :disabled="votingByThisRound"
                   class="button-group__button_green"
                   @click="checkStillLife(true)">–ù–∞—Ç—é—Ä–º–æ—Ä—Ç</AppButton>
        <AppButton :disabled="votingByThisRound"
                   class="button-group__button_red"
                   @click="checkStillLife(false)">–ù–µ –Ω–∞—Ç—é—Ä–º–æ—Ä—Ç</AppButton>
      </AppButtonGroup>
    </div>
</template>

<script>
import json from '@/json/game_1.json';
import GameFirstData from '@/models/GameFirstData';
import Sprite from '@/models/Sprite';
import resource from '@/helper/Resource';
import EvilGoblin from '@/models/EvilGoblin';
import AppScope from '@/components/common/AppScope';
import AppButtonGroup from '@/components/common/AppButtonGroup';
import AppButton from '@/components/common/AppButton';
import AppTimer from '@/components/common/AppTimer';

export default {
  name: 'LevelOne',
  components: {
    AppTimer, AppButton, AppButtonGroup, AppScope,
  },
  props: {
    levelFinish: {
      type: Boolean,
      require: true,
    },
  },
  data() {
    const gameData = json.map(item => new GameFirstData(item));
    return {
      frameId: null,
      canvas: null,
      context: null,
      gameData,
      lastTime: null,
      gameTime: 0,
      image: [],
      timeEnd: 60,
      speed: 300,
      goblin: null,
      votingByThisRound: false,
      ramka: null,
    };
  },

  methods: {
    init() {
      this.goblin = new EvilGoblin({});
      const { $refs } = this;
      this.canvas = $refs.game;
      const { canvas } = this;
      if (canvas.getContext) {
        canvas.width = document.body.clientWidth;
        canvas.height = document.body.clientHeight;
        this.context = canvas.getContext('2d');
      }
      this.setBackground();
      this.setRamka();
      this.setGoblin();
      this.updateGame();
      this.addElement(true); // TODO –î–æ–±–∞–≤–ª–µ–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –Ω–∞ –ø–æ–ª–µ
      this.updateTimer();
    },

    checkStillLife(value) {
      const currentImg = this.image[0];

      if (!this.votingByThisRound) {
        if (currentImg.gameObject.isStillLife === value) {
          this.$store.commit('addScope');
          this.goblin.updateState('angry');
        } else {
          this.goblin.updateState('happy');
        }
        this.speed *= 3;
      }
      this.votingByThisRound = true;
    },

    setBackground() {
      const { context } = this;
      context.drawImage(resource.get('/evil_goblin//img/bg/bg-1.jpg'), 0, 0);
    },
    setRamka() {
      const size = [390, 540];
      const sizeImg = [1360, 860];
      this.ramka = new Sprite({
        url: '/evil_goblin//img/puzzle/frame-puzzle.png',
        pos: [0, 0],
        size,
        sizeImg,
        positionInCanvas: this.setCenter(size),
        speed: 6,
        frames: [0],
        dir: 'vertical',
      });
    },


    setGoblin() {
      this.goblin.animate(this.context);
    },


    updateRamka(dt) {
      this.ramka.update(dt, this.speed);
    },
    updateTimer() {
      setInterval(() => { this.timeEnd -= 1; }, 1000);
    },

    updateGame() {
      if (this.levelFinish) {
        return;
      }
      const now = Date.now();
      const dt = (now - this.lastTime) / 1000.0;

      this.update(dt);
      this.render();
      this.renderRamka(this.image[0]);

      this.lastTime = now;
      this.frameId = window.requestAnimationFrame(this.updateGame);
    },
    update(dt) {
      const { context, canvas } = this;
      this.gameTime += dt;

      context.clearRect(0, 0, canvas.width, canvas.height);
      this.setBackground();

      this.setGoblin();
      this.updateRamka(dt);
      this.updateEntities(dt);
      this.isFinish();
    },

    isFinish() {
      if (this.timeEnd <= 0) {
        this.$emit('levelFinish', 1);
        // this.levelFinish = true;
      }
    },

    updateEntities(dt) {
      // Update all the image
      for (let i = 0; i < this.image.length; i++) {
        this.image[i].sprite.update(dt, this.speed);

        // Remove if offscreen
        if (this.image[i].sprite.posInCanvas[1] > document.body.clientHeight) {
          this.image.splice(i, 1);
          this.addElement();
          i--;
        }
      }
    },

    addElement(isStart) {
      const indexNewCard = this.randomIntFromInterval(0, this.gameData.length - 1);
      const newEntries = this.gameData[indexNewCard];

      if (newEntries) {
        if (!this.votingByThisRound && !isStart) {
          this.goblin.updateState('happy');
        }
        this.votingByThisRound = false;
        const size = [352, 480];
        this.image.push({
          gameObject: newEntries,
          sprite: new Sprite({
            url: newEntries.img,
            pos: [0, 0],
            size,
            positionInCanvas: this.setCenter(size),
            speed: 6,
            frames: [0],
            dir: 'horizontal',
          }),
        });
      }
      this.speed = 200;
    },
    randomIntFromInterval(min, max) {
      return Math.floor(Math.random() * (max - min + 1) + min);
    },

    setRandomPosition() {
      return [Math.random() * (this.canvas.width - 350),
        0 - 480];
    },

    setCenter(size) {
      return [document.body.clientWidth / 2 - (size[0] / 2), -size[1] - 20];
    },

    render() {
      this.renderEntities(this.image);
    },

    renderEntities(list) {
      for (let i = 0; i < list.length; i++) {
        this.renderEntity(list[i]);
      }
    },

    renderRamka(entity) {
      if (entity) {
        const { context } = this;
        context.save();
        context.translate(entity.sprite.posInCanvas[0] - 25, entity.sprite.posInCanvas[1] - 30);
        this.ramka.render(context);
        context.restore();
      }
    },
    renderEntity(entity) {
      const { context } = this;
      context.save();
      context.translate(entity.sprite.posInCanvas[0], entity.sprite.posInCanvas[1]);
      entity.sprite.render(context);
      context.restore();
    },
  },
  mounted() {
    resource.onReady(this.init);
  },
  beforeDestroy() {
    cancelAnimationFrame(this.frameId);
  },
};
</script>

<style lang="scss" scoped>
  .game {
    position: relative;
    flex-grow: 1;
    overflow: hidden;
  }
</style>
