import resource from '@/helper/Resource';

export default class Sprite {
  constructor({
    url, pos, size, speed, frames, dir, once, positionInCanvas,
    sizeImg,
  }) {
    // url, pos, size, speed, frames, dir, once
    this.url = url;
    const img = resource.get(this.url);
    this.sizeImg = [img.width, img.height];
    this.posInCanvas = positionInCanvas;
    this.pos = pos;
    this.size = size;
    this.speed = typeof speed === 'number' ? speed : 0;
    this.frames = frames;
    this._index = 0;
/*    if (sizeImg) {
      this.sizeImg = sizeImg;
    } else {
      this.sizeImg = size;
    }*/
    this.dir = dir || 'vertical';
    this.once = once;
  }

  update(dt, speed) {
    this._index += this.speed * dt;
    this.posInCanvas[1] += speed * dt; // Y drop
  }

  render(ctx) {
    let frame;
    if (this.speed > 0) {
      const max = this.frames.length;
      const idx = Math.floor(this._index);
      frame = this.frames[idx % max];

      if (this.once && idx >= max) {
        this.done = true;
        return;
      }
    } else {
      frame = 0;
    }


    let x = this.pos[0];
    let y = this.pos[1];

    if (this.dir === 'vertical') {
      y += frame * this.size[1];
    } else {
      x += frame * this.size[0];
    }

    ctx.drawImage(resource.get(this.url),
      x, y,
      this.sizeImg[0], this.sizeImg[1],
      0, 0,
      this.size[0], this.size[1]);
  }
}
