class Resource {
  constructor() {
    this.resourceCache = {};
    this.loading = [];
    this.readyCallbacks = [];
  }

  load(urlOrArr) {
    if (urlOrArr instanceof Array) {
      urlOrArr.forEach((url) => {
        this.saveToCache(url);
      });
    } else {
      this.saveToCache(urlOrArr);
    }
  }

  saveToCache(url) {
    if (this.resourceCache[url]) {
      return this.resourceCache[url];
    }

    const img = new Image();
    img.onload = () => {
      this.resourceCache[url] = img;
      this.initFunction();
    };
    this.resourceCache[url] = false;
    img.src = url;
  }

  initFunction() {
    if (this.isReady()) {
      this.readyCallbacks.forEach((func, index) => {
        func();
        this.readyCallbacks.splice(index);
      });
    }
  }

  get(url) {
    return this.resourceCache[url];
  }

  isReady() {
    let ready = true;
    for (const k in this.resourceCache) {
      if (this.resourceCache.hasOwnProperty(k)
        && !this.resourceCache[k]) {
        ready = false;
      }
    }
    return ready;
  }

  onReady(func) {
    setTimeout(() => {
      this.readyCallbacks.push(func);
      this.initFunction();
    }, 10);
  }
}

// Load an image url or an array of image urls


const resource = new Resource();
export default resource;
